import React, { useMemo } from 'react'
import PropTypes from 'prop-types'

import { Typography, Divider } from 'antd'

const { Title } = Typography;

function ProjectCard({ image, title, description, skill, link, github }) {

  const linkStyle = useMemo(() => ({
    marginRight: 10,
    padding: 12,
    border: '1px solid #191b1e',
    borderRadius: 30,
    backgroundColor: '#191b1e',
    color: '#ffffff',
    fontWeight: 700,
    textDecoration: 'none'
  }), [])

  return (
    <div style={{ display: 'flex', alignItems: 'center', margin: '120px 30px' }}>
      <div style={{ width: '35vw', maxWidth: 600 }}>
        <img src={image} width='100%' />
      </div>
      <div style={{ marginLeft: 100 }}>
        <Title level={3}>
          {title}
        </Title>
        <Divider />
        <Title level={4}>
          {description}
        </Title>
        {skill}
        <br />
        <a style={linkStyle} href={link} target="_blank" rel="noreferrer noopener">사용하기</a>
        <a style={linkStyle} href={github} target="_blank" rel="noreferrer noopener">Github</a>
      </div>
    </div >
  )
}

ProjectCard.propTypes = {
  image: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  skill: PropTypes.node.isRequired,
  link: PropTypes.string.isRequired,
  github: PropTypes.string.isRequired,
}

export default ProjectCard
